<template>
  <div class="main-wrap">
    <div class="title">
      <span class="left">{{userid == undefined ? "" : ""}}</span>
      <span class="right"><i>*</i></span>
    </div>
    <el-form :model="form" :rules="FormRules" label-width="100px" ref="formName" v-loading="formLoading">
      <el-form-item prop="loginName" label="">
        <el-input v-model.trim="form.loginName" autocomplete="off" maxlength=32 placeholder=""></el-input>
      </el-form-item>
      <el-form-item v-if="!userid" prop="password" label="">
        <el-input type="text"  onfocus="this.type='password'"  autocomplete="off" v-model="form.password" maxlength="20" placeholder="8~20_#@!~%^&*"></el-input>
      </el-form-item>
      <el-form-item v-if="userid" label="">
        <el-input type="password" autocomplete="off" :disabled="true" v-model="form.password"  maxlength="20" placeholder="8~20_#@!~%^&*"></el-input>
      </el-form-item>
      <el-form-item prop="name" label="">
        <el-input v-model.trim="form.name" placeholder="" :title="form.name" maxlength=32></el-input>
      </el-form-item>
      <el-form-item prop="sex" label="">
        <el-radio-group v-model="form.sex">
          <el-radio :label="'1'"></el-radio>
          <el-radio :label="'0'"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="phone" label="">
        <el-input v-model="form.phone" placeholder="" maxlength=11></el-input>
      </el-form-item>
      <el-form-item prop="email" label="">
        <el-input v-model="form.email" placeholder="" :title="form.email"  maxlength=32></el-input>
      </el-form-item>
      <el-form-item prop="roleId" label="">
        <el-select v-model="form.roleId" placeholder=""  :disabled="userid">
          <el-option v-for="(item,index) in roledata" :key="index" :label="item.name" :value="item.roleId"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="resetForm" v-if="!userid"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { addUser, userDetail, editUser, roleList } from "../../api/api";
import md5 from "js-md5";
export default {
  data() {
    return {
      // 
      form: {
        loginName: "",
        password: "",
        name: "",
        sex: "",
        phone: "",
        email: "",
        roleId: ""
      },
      FormRules: {
        loginName: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^(?=.*\d)(?=.*[a-zA-Z])(?=.*[~!@#$%^&*_])[\da-zA-Z~!@#$%^&*_]{8,20}$/,
            message: "8~20_#@!~%^&*",
            trigger: "blur"
          }
        ],
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "",
            trigger: "blur"
          }
        ],
        sex: [{ required: true, message: "", trigger: "blur" }],
        phone: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        email: [
          { type: "email", message: "", trigger: "blur" }
        ],
        roleId: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      roledata: [],
      userid: "",
      formLoading: false
    };
  },
  mounted() {
    // userid
    this.userid = this.$route.query.userid;
    this.mianload();
    this.getRoleData();
  },
  methods: {
    // 
    mianload() {
      if (this.userid != undefined) {
        let para = {
          userId: this.userid
        };
        this.formLoading = true;
        userDetail(para).then(res => {
          if (res.header.code !== 10000000) {
            this.$message({
              offset: 100,
              message: res.header.message,
              type: "error"
            });
          } else {
            this.form.loginName = res.body.loginName;
            this.form.name = res.body.name;
            this.form.sex = res.body.sex;
            this.form.phone = res.body.phone;
            this.form.email = res.body.email;
            this.form.roleId = res.body.roleId;
            this.form.password = res.body.password; //
          }
          this.formLoading = false;
        });
      } else {
        this.form.loginName = "";
        this.form.name = "";
        this.form.sex = "1";
        this.form.phone = "";
        this.form.email = "";
        this.form.roleId = "";
        this.form.password = ""; //
      }
    },
    //
    getRoleData() {
      let para = {
        current: "1",
        size: "1000"
      };
      roleList(para).then(res => {
        if (res.header.code !== 10000000) {
          this.$message({
            offset: 100,
            message: res.header.message,
            type: "error"
          });
        } else {
          this.roledata = res.body.records;
        }
      });
    },
    // 
    onSubmit() {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          if (this.userid) {
            // 
            let para = {
              userId:this.userid,
/*               photo:''	,//	string	V32		
              corpId:'',	//	string	V32		
              vspCorpId:'',	//	string	V32	id	
              number:'',	//	string	V32		 */
              name:this.form.name,	//	string	V32	
              loginName:this.form.loginName,
              email:this.form.email,
              phone:this.form.phone,
/*               loginFlag:'',//		string	V32	 0 1	
              userType:'',//		string	V32	1 2 3 4 5gms	
              remark:'',//		string	V32	 */
              sex:this.form.sex
            };
/*             para.password = md5(this.form.password);
            para.userId = this.userid; */
            editUser(para).then(res => {
              if (res.header.code !== 10000000) {
                this.$message({
                  offset: 100,
                  message: res.header.message,
                  type: "error"
                });
              } else {
                this.$message({
                  offset: 100,
                  type: "success",
                  message: ""
                });
                let _this = this;
                setTimeout(function() {
                  _this.$router.push({
                    path: "/userset"
                  });
                }, 1000);
              }
            });
          } else {
            // 
             let para = {
/*               photo:''	,//	string	V32		
              corpId:'',	//	string	V32		
              vspCorpId:'',	//	string	V32	id	
              number:'',	//	string	V32		 */
              name:this.form.name,	//	string	V32	
              loginName:this.form.loginName,
              password:md5(this.form.password),
              email:this.form.email,
              phone:this.form.phone,
              roleId:this.form.roleId,
/*               loginFlag:'',//		string	V32	 0 1	
              userType:'',//		string	V32	1 2 3 4 5gms	
              remark:'',//		string	V32	 */
              sex:this.form.sex
            };
            //para.password = md5(this.form.password);
            addUser(para).then(res => {
              if (res.header.code !== 10000000) {
                this.$message({
                  offset: 100,
                  message: res.header.message,
                  type: "error"
                });
              } else {
                this.$message({
                  offset: 100,
                  type: "success",
                  message: ""
                });
                let _this = this;
                setTimeout(function() {
                  _this.$router.push({
                    path: "/userset"
                  });
                }, 1000);
              }
            });
          }
        } else {
          this.$message({
            offset: 100,
            type: "warning",
            message: ""
          });
        }
      });
    },
    // 
    resetForm() {
      if (this.userid == undefined) {
        this.$refs["formName"].resetFields();
      } else {
        this.mianload();
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    let useridx = to.query.userid;
    if (useridx == undefined) {
      to.meta.title = "";
    } else {
      to.meta.title = "";
    }
    next();
  }
};
</script>
<style lang="less" scoped>
.main-wrap {
  background: #ffffff;
  .title {
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
    border-bottom: 1px solid #e9e9e9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      font-weight: 700;
      font-size: 16px;
    }
    .right {
      color: #999999;
      i {
        margin-right: 5px;
        color: red;
      }
    }
  }
  .el-form {
    padding: 50px 100px;
    .el-form-item {
      .el-input {
        width: 60%;
      }
      .el-select {
        width: 60%;
      }
    }
  }
}
</style>


