<template>
  <section>

    <!--  -->
    <el-col :span="24" class="search">
      <div class="search-title title"></div>
      <el-form :inline="true">
        <el-form-item label="">
          <el-input v-model="search_car1" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="search_time1" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="5" value="5"></el-option>
            <el-option label="10" value="10"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="search_state1" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="fa fa-search fa-fw" @click="SearchData"> </el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!--  -->
    <el-col :span="24" class="table">
      <div class="table-title title">
        
      </div>
      <el-table ref="multipleTable" border :data="users" highlight-current-row v-loading="listLoading"
        @selection-change="handleSelectionChange">
        <el-table-column fixed="left" type="selection" align="center" width="50">
        </el-table-column>
        <el-table-column align="center" sortable prop="orderId" label="">
        </el-table-column>
        <el-table-column align="center" sortable prop="creatTime" label="">
        </el-table-column>
        <el-table-column align="center" sortable prop="customerTel" label="">
        </el-table-column>
        <el-table-column align="center" sortable prop="carNum" label="">
        </el-table-column>
        <el-table-column align="center" sortable label="" width="150">
          <template slot-scope="scope">
            <span :class="Number(scope.row.timeLeft) < 1500 ? 'isred' : ''">
              {{ scope.row.timeLeft | getDuration }}
            </span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            {{ scope.row.status == '1' ?  '': '' }}
            {{ scope.row.status == '2' ?  '': '' }}
            {{ scope.row.status == '3' ?  '': '' }}
          </template>
        </el-table-column>
        <el-table-column align="center" fixed="right" label=""
          v-if="power.indexOf(',,') > -1 || power.indexOf(',,') > -1 || power.indexOf(',,') > -1">
          <template slot-scope="scope">
            <el-button v-if="power.indexOf(',,') > -1" class="el-button--table" size="small" icon="fa fa-pencil fa-fw"
              @click="handleEdit(scope.$index, scope.row)">
            </el-button>
            <el-button v-if="power.indexOf(',,') > -1" class="el-button--table" size="small"
              @click="handleDetail(scope.$index, scope.row)">
            </el-button>
<!--             <el-button v-if="power.indexOf(',,') > -1" class="el-button--table" size="small"
              @click="handleRefresh(scope.$index, scope.row)">
            </el-button> -->
          </template>
        </el-table-column>
      </el-table>
    </el-col>

    <!---->
    <el-col :span="24" class="toolbar">
      <div class="tool-left" v-if="power.indexOf(',,') > -1">
        <el-button icon="fa fa-check-square-o fa-fw" @click="allSelection()" :disabled="users.length == 0">
        </el-button>
        <el-button icon="fa fa-square-o fa-fw" @click="reverseSelection()" :disabled="users.length == 0"></el-button>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="power.indexOf(',,') > -1" :disabled="sels.length == 0"
              @click.native="handleallState"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 20, 50]"
        :current-page="page" :page-size="pagesize" layout="sizes, prev, pager, next" :total="total">
      </el-pagination>
    </el-col>

    <!--  -->
    <el-dialog title="" :before-close="change_cancel" :visible.sync="OrderFormVisible"
      :close-on-click-modal="false" width="600px">
      <el-form :model="change_form" :rules="changeRules" class="el-change" label-width="100px" ref="formName">
        <el-form-item prop="status" label="">
          <el-select v-model="change_form.status" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="remarks" label="">
          <el-input type="textarea" placeholder="" maxlength="300" @input="change_num"
            v-model="change_form.remarks">
          </el-input>
          <i>{{stringnum}}/300</i>
        </el-form-item>
        <el-form-item>
          <el-button :type="!change_form.remarks || !change_form.status?'info':'primary'" @click="change_submit">
          </el-button>
          <el-button @click="change_cancel"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

  </section>
</template>

<script>
import {
  warnList,
  refreshOrder,
  warnOrderStatus,
  batchWarnOrderStatus
} from "../../api/api";
import { mapState } from "vuex";
export default {
  data() {
    return {
      // 
      search_car: "",
      search_time: "",
      search_state: "",
      search_car1: "",
      search_time1: "",
      search_state1: "",

      // 
      users: [],
      sels: [], //

      // 
      total: 0,
      page: 1,
      pagesize: 10,

      // 
      OrderFormVisible: false,
      change_form: {
        orderWarnId: "",
        status: "",
        remarks: ""
      },
      changeRules: {
        status: [{ required: true, message: "", trigger: "blur" }],
        remarks: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "",
            trigger: "blur"
          }
        ]
      },
      stringnum: 0,
      // 
      power: ",",
      // 
      timer: "",

      // loading
      listLoading: false
    };
  },
  computed: {
    ...mapState({
      powerlist: state => state.power
    })
  },
  filters: {
    // 
    getDuration(value) {
      let minutes = Math.floor(((value % 86400) % 3600) / 60);
      let seconds = Math.floor(((value % 86400) % 3600) % 60);
      if (Number(value) < 0) {
        minutes = 0;
        seconds = 0;
      }
      let duration = minutes + "" + seconds + "";
      return duration;
    }
  },
  methods: {
    //
    getUsers(callback) {
      let para = {
        current: this.page,
        size: this.pagesize,
        status: this.search_state,
        carNum: this.search_car,
        timeLeft: this.search_time
      };
      this.listLoading = true;
      warnList(para).then(res => {
        //
        if(callback){
          callback(res);
        }
        if (res.header.code !== 10000000) {
          this.$message({
            offset: 100,
            message: res.header.message,
            type: "error"
          });
        } else {
          this.users = (res.body&&res.body.records)?res.body.records:[];
          this.total = (res.body&&res.body.total)?res.body.total:0;
        }
        this.listLoading = false;
      });
    },

    // 
    handleSizeChange(val) {
      this.pagesize = val;
      this.getUsers();
    },

    // 
    handleCurrentChange(val) {
      this.page = val;
      this.getUsers();
    },
    // 
    SearchData() {
      this.search_car = this.search_car1;
      this.search_time = this.search_time1;
      this.search_state = this.search_state1;
      this.page=1;
      this.getUsers();
    },
    // 
    allSelection() {
      this.$refs.multipleTable.toggleAllSelection(this.users);
    },

    // 
    reverseSelection() {
      this.users.forEach(row => {
        this.$refs.multipleTable.toggleRowSelection(row);
      });
    },

    // 
    handleSelectionChange(sels) {
      this.sels = sels;
    },

    //
    handleDetail(index, row) {
      this.$router.push({
        path: "/orderinfo",
        query: { orderid: row.orderId }
      });
    },

    // 
    change_num() {
      this.stringnum = this.change_form.remarks.length;
    },

    // 
    change_submit() {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          if (this.stringnum > 300) {
            this.$message({
              offset: 100,
              type: "warning",
              message: "300"
            });
          } else {
            let para = this.change_form;
            this.listLoading = true;
            if (this.change_form.orderWarnId) {
              // 
              warnOrderStatus(para).then(res => {
                if (res.header.code !== 10000000) {
                  this.$message({
                    offset: 100,
                    message: res.header.message,
                    type: "error"
                  });
                } else {
                  this.$message({
                    offset: 100,
                    message: "",
                    type: "success"
                  });
                  this.change_cancel();
                  this.getUsers();
                }
                this.listLoading = false;
              });
            } else {
              // 
              para.orderWarnId = [];
              for (let i = 0; i < this.sels.length; i++) {
                para.orderWarnId.push(this.sels[i].orderWarnId);
              }
              batchWarnOrderStatus(para).then(res => {
                if (res.header.code !== 10000000) {
                  this.$message({
                    offset: 100,
                    message: res.header.message,
                    type: "error"
                  });
                } else {
                  this.$message({
                    offset: 100,
                    message: "",
                    type: "success"
                  });
                  this.change_cancel();
                  this.getUsers();
                }
                this.listLoading = false;
              });
            }
          }
        } else {
          this.$message({
            offset: 100,
            type: "warning",
            message: ""
          });
        }
      });
    },

    // 
    change_cancel() {
      this.OrderFormVisible = false;
      this.change_form = {
        orderWarnId: "",
        status: "",
        remarks: ""
      };
      this.stringnum = 0;
    },

    // 
    handleEdit(index, row) {
      console.log(row);
      this.change_form.orderWarnId = row.orderWarnId;
      this.OrderFormVisible = true;
    },

    // 
    handleRefresh(index, row) {
      let para = {
        orderId: row.orderId
      };
      refreshOrder(para).then(res => {
        if (res.header.code !== 10000000) {
          this.$message({
            offset: 100,
            message: res.header.message,
            type: "error"
          });
        } else {
          this.$message({
            offset: 100,
            message: "",
            type: "success"
          });
          this.getUsers();
        }
      });
    },

    //
    handleallState() {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.OrderFormVisible = true;
        })
        .catch(() => {});
    }
  },
  mounted() {
    let _this = this;
    this.getUsers(res=>{
    this.timer = setInterval(function() {
      _this.getUsers();
    }, 60*1000);
    });
    // 
    for (let i = 0; i < this.powerlist.length; i++) {
      for (let j = 0; j < this.powerlist[i].children.length; j++) {
        for (
          let c = 0;
          c < this.powerlist[i].children[j].children.length;
          c++
        ) {
          if (this.powerlist[i].children[j].href == this.$route.path) {
            this.power += this.powerlist[i].children[j].children[c].name + ",";
          }
        }
      }
    }

  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style lang="less" scoped>
section {
  .title {
    height: 50px;
    line-height: 50px;
    font-weight: 700;
    font-size: 16px;
  }
  .search {
    width: 100%;
    border: 1px solid #e9e9e9;
    background: #fff;
    margin-bottom: 20px;
    .search-title {
      padding: 0 20px;
      border-bottom: 1px solid #e9e9e9;
    }
    .el-form {
      .el-form-item {
        margin: 20px;
        .el-input {
          width: 150px;
        }
        .el-select {
          width: 150px;
        }
        .el-button {
          padding-left: 8px;
        }
      }
    }
  }
  .table {
    padding: 10px 20px;
    background: #fff;
    .isred {
      color: #fb6260;
    }
  }
  .toolbar {
    height: 80px;
    line-height: 80px;
    padding: 0 20px;
    background: #fff;
    .tool-left {
      float: left;
      .el-dropdown {
        height: 38px;
        line-height: 38px;
        text-indent: 10px;
        width: 100px;
        border: 1px solid #dcdfe6;
        margin-left: 20px;
        border-radius: 4px;
        color: #606266;
      }
    }
    .el-pagination {
      float: right;
      height: 30px;
      padding: 25px 0;
    }
  }
  .el-dialog {
    .el-form-item {
      position: relative;
      .el-select {
        width: 80%;
      }
      .el-textarea {
        width: 80%;
        height: 120px;
        border: 1px solid #d7d7d7;
        border-radius: 3px;
      }
      i {
        position: absolute;
        bottom: 20px;
        right: 96px;
        color: #e0e0e0;
        height: 10px;
      }
    }
    .el-form-item:last-child {
      text-align: right;
      padding-right: 100px;
    }
  }
}
</style>


