<template>
  <section>
    <!--  -->
    <el-col class="part">
      <div class="all-title">
        <span class="title-left"><i class="el-icon-warning"></i>{{orderstatus | orderstate}}</span>
        <span class="title-right" @click="copyinfo"></span>
      </div>
      <div class="table">
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="orderId" align="center" label="">
          </el-table-column>
          <el-table-column prop="customerName" align="center" label="">
          </el-table-column>
          <el-table-column prop="customerTel" align="center" label="">
          </el-table-column>
          <el-table-column prop="payPath" align="center" label="">
          </el-table-column>
          <el-table-column align="center" label="">
            <template slot-scope="scope">
              {{ scope.row.bussinessType == "1" ?  '' : '' }}
              {{ scope.row.bussinessType == "2" ?  '' : '' }}
              {{ scope.row.bussinessType == "3" ?  '' : '' }}
              {{ scope.row.bussinessType == "4" ?  '' : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="remarks" align="center" label="">
          </el-table-column>
        </el-table>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column align="center" label="">
            <template slot-scope="scope">
              {{ scope.row.serviceType == "1" ?  '' : '' }}
              {{ scope.row.serviceType == "2" ?  '' : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="orderChannel" align="center" label="">
          </el-table-column>
          <el-table-column prop="dirverName" align="center" label="">
          </el-table-column>
          <el-table-column prop="driverDid" align="center" label="">
          </el-table-column>
          <el-table-column prop="driverMob" align="center" label="">
          </el-table-column>
          <el-table-column prop="appraisal" align="center" label="">
          </el-table-column>
        </el-table>
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="startMan" align="center" label="">
          </el-table-column>
          <el-table-column prop="startManTel" align="center" label="">
          </el-table-column>
          <el-table-column prop="startPoiName" align="center" label="">
          </el-table-column>
          <el-table-column prop="startPoiAddress" align="center" label="">
          </el-table-column>
        </el-table>
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="endMan" align="center" label="">
          </el-table-column>
          <el-table-column prop="endManTel" align="center" label="">
          </el-table-column>
          <el-table-column prop="endPoiName" align="center" label="">
          </el-table-column>
          <el-table-column prop="endPoiAddress" align="center" label="">
          </el-table-column>
        </el-table>
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="carNum" align="center" label="">
          </el-table-column>
          <el-table-column prop="frameNum" align="center" label="">
          </el-table-column>
          <el-table-column prop="engineNum" align="center" label="">
          </el-table-column>
        </el-table>
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="orderMoney" align="center" label="">
          </el-table-column>
          <el-table-column prop="attachMoney" align="center" label="">
          </el-table-column>
          <el-table-column prop="couponMoney" align="center" label="">
          </el-table-column>
          <el-table-column prop="score" align="center" label="">
          </el-table-column>
        </el-table>
        <el-table :data="all_table" border v-loading="listLoading">
          <el-table-column prop="eventOffer" align="center" label="">
          </el-table-column>
          <el-table-column prop="discountMoney" align="center" label="">
          </el-table-column>
          <el-table-column prop="totalMoney" align="center" label="">
          </el-table-column>
          <el-table-column prop="payableMoney" align="center" label="">
          </el-table-column>
        </el-table>
        <p class="each-title">
          <i class="fa fa-bookmark"></i>
          <span></span>
        </p>
        <el-table :data="operate_data" border empty-text="" v-loading="listLoading1">
          <el-table-column prop="createBy" align="center" label="">
          </el-table-column>
          <el-table-column align="center" label="">
            <template slot-scope="scope">
              {{scope.row.orderStatus | orderstate}}
            </template>
          </el-table-column>
          <el-table-column prop="createTime" align="center" label="">
          </el-table-column>
          <el-table-column prop="remarks" align="center" label="">
          </el-table-column>
        </el-table>
      </div>
    </el-col>
  </section>
</template>

<script>
import { orderDetail, orderOperate } from "../../api/api";
export default {
  data() {
    return {
      // 
      orderid: "",
      // 
      orderstatus: "",
      // 
      all_table: [],
      // 
      operate_data: [],
      // loading
      listLoading: false,
      listLoading1: false
    };
  },
  filters: {
    orderstate(val) {
      if (val == "10") {
        return "";
      }
      if (val == "20") {
        return "";
      }
      if (val == "30") {
        return "";
      }
      if (val == "40" || val == "41") {
        return "";
      }
      if (val == "50") {
        return "";
      }
      if (val == "60" || val == "61") {
        return "";
      }
      if (val == "70") {
        return "";
      }
      if (val == "80" || val == "81") {
        return "";
      }
    }
  },
  methods: {
    //
    getTableinfo() {
      this.listLoading = true;
      let para = {
        orderId: this.orderid
      };
      orderDetail(para).then(res => {
        if (res.header.code !== 10000000) {
          this.$message({
            offset: 100,
            message: res.header.message,
            type: "error"
          });
        } else {
          this.all_table.push(res.body);
          this.orderstatus = res.body.orderStatus;
        }
        this.listLoading = false;
      });
    },

    // 
    getorderOperate() {
      this.listLoading1 = true;
      let para = {
        current: "1",
        size: "1000",
        orderId: this.orderid
      };
      orderOperate(para).then(res => {
        if (res.header.code !== 10000000) {
          this.$message({
            offset: 100,
            message: res.header.message,
            type: "error"
          });
        } else {
          this.operate_data = res.body.records;
        }
        this.listLoading1 = false;
      });
    },
    // 
    copyinfo() {
      let nowtime = new Date();
      let creatime = this.all_table[0].createTime;
      let date = nowtime.getTime() - new Date(creatime).getTime(); //
      //
      let leave = date % (3600 * 1000); //
      let minutes = Math.floor(leave / (60 * 1000));
      //
      let leavex = leave % (60 * 1000); //
      let seconds = Math.round(leavex / 1000);
      let differtime = minutes + " " + seconds + " ";
      // 
      let copyStr =
        "" +
        this.all_table[0].orderId +
        "" +
        this.all_table[0].createTime +
        "" +
        differtime +
        "";
      // input
      const oInput = document.createElement("input");
      // input
      oInput.value = copyStr;
      document.body.appendChild(oInput);
      // input
      oInput.select();
      //  bool
      const copyResult = document.execCommand("copy");
      //  Dominput
      document.body.removeChild(oInput);
      //  
      if (copyResult) {
        this.$message({
          offset: 100,
          message: "",
          type: "success"
        });
      } else {
        this.$message({
          offset: 100,
          message: "",
          type: "error"
        });
      }
    }
  },
  mounted() {
    this.orderid = this.$route.query.orderid;
    this.getTableinfo();
    this.getorderOperate();
  }
};
</script>

<style lang="less" scoped>
section {
  .step {
    width: 100%;
    height: 200px;
    background: #fff;
    .el-steps {
      height: 100px;
      padding: 50px 0;
    }
  }
  .part {
    background: #fff;
    margin: 20px 0;
    .all-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      border-bottom: 1px solid #e9e9e9;
      .title-left {
        height: 60px;
        line-height: 60px;
        padding: 0 50px;
        font-size: 17px;
        color: #f04844;
        font-weight: 600;
      }
      .title-right {
        margin-right: 20px;
        background: #0079fe;
        color: #fff;
        padding: 5px 10px;
        border-radius: 5px;
      }
      .title-right:hover {
        background: #409eff;
      }
    }
    .table {
      padding: 20px 50px;
      .each-title {
        font-size: 16px;
        font-weight: 600;
        margin-top: 30px;
      }
    }
  }
}
</style>


