import Vue from 'vue'
import Router from 'vue-router'
// 
import Home from '@/components/Home'
// 404
import NotFound from '@/components/404'
// 
import Login from '@/components/login/login'
// -
import OrderList from '@/components/orderList/orderlist'
// -
import OrderInfo from '@/components/orderList/orderinfo'
// -
import WarningOrder from '@/components/orderList/warningorder'
// -
import ActiveList from '@/components/operationSet/activelist'
// -/
import FoundActive from '@/components/operationSet/foundactive'
// -
import StoreList from '@/components/operationSet/storelist'
// -/
import AddStore from '@/components/operationSet/addstore'
// -
import Configuration from '@/components/operationSet/configuration'
// -
import RoleSet from '@/components/systemSet/roleset'
// -
import PowerSet from '@/components/systemSet/powerset'
// -
import UserSet from '@/components/systemSet/userset'
// -/
import AddOrEdit from '@/components/systemSet/addoredit'
// -
import LoginRecord from '@/components/systemSet/loginrecord'
// -
import ChangePass from '@/components/user/changepass'

Vue.use(Router)

export default new Router({
  routes: [
  {
    path: '/404',
    component: NotFound,
    name: 'NotFound',
    meta:{
      title: '404',
      hidden: true
    }
  },
  {
    path: '/login',
    component: Login,
    name: 'Login',
    meta:{
      title: '',
      needLogin: false,
      hidden: true
    }
  },
  {
    path: '/',
    component: Home,
    name: 'Home',
    meta:{
      title: '',
      needLogin: true,
      hidden: true
    }
  },
  {
    path: '/',
    component: Home,
    name: 'Home',
    meta:{
      title: '',
      iconCls: 'fa fa-list-alt fa-fw',//class
    },
    children: [
        { path: '/orderlist', component: OrderList, name: 'OrderList', meta:{ title: '', needLogin: true}},
        { path: '/orderinfo', component: OrderInfo, name: 'OrderInfo', meta:{ title: '', hidden: true, needLogin: true}},
        { path: '/warningorder', component: WarningOrder, name: 'WarningOrder', meta:{ title: '', needLogin: true}},
    ]
  },
  {
    path: '/',
    component: Home,
    name: 'Home',
    meta:{
      title: '',
      iconCls: 'fa fa-television fa-fw',//class
    },
    children: [
      { path: '/activelist', component: ActiveList, name: 'ActiveList', meta:{ title: '', needLogin: true}},
      { path: '/foundactive', component: FoundActive, name: 'FoundActive', meta:{ title: '/', hidden: true, needLogin: true}},
      { path: '/storelist', component: StoreList, name: 'StoreList', meta:{ title: '', needLogin: true}},
      { path: '/addstore', component: AddStore, name: 'AddStore', meta:{ title: '/', hidden: true, needLogin: true}},
      { path: '/configuration', component: Configuration, name: 'Configuration', meta:{ title: '', needLogin: true}},
    ]
  },
  {
    path: '/',
    component: Home,
    name: 'Home',
    meta:{
      title: '',
      iconCls: 'fa fa-cog fa-fw',//class
    },
    children: [
      { path: '/roleset', component: RoleSet, name: 'RoleSet', meta:{ title: '', needLogin: true}},
      { path: '/powerset', component: PowerSet, name: 'PowerSet', meta:{ title: '', hidden: true, needLogin: true}},
      { path: '/userset', component: UserSet, name: 'UserSet', meta:{ title: '', needLogin: true}},
      { path: '/addoredit', component: AddOrEdit, name: 'AddOrEdit', meta:{ title: '/', hidden: true, needLogin: true}},
      { path: '/loginrecord', component: LoginRecord, name: 'LoginRecord', meta:{ title: '', needLogin: true}}
    ]
  },
  {
    path: '/',
    component: Home,
    name: 'Home',
    meta:{
      title: '',
      iconCls: 'fa fa-user fa-fw',//class
      hidden: true
    },
    children: [
        { path: '/changepass', component: ChangePass, name: 'ChangePass', meta:{ title: '', needLogin: true}}
    ]
  },
  ]
})



