let fetchcar = process.env.SERV_ADDR; //  config/*.env.js

let toLogin =_debounce(function(){ //5
  _this.$message({
    offset: 100,
    message: 'TOKEN,',
    type: "warning"
  })
  setTimeout(function(){
    _this.$router.replace({
      path:'/login'
    })
  },2*1000)
},5*1000)

