<template>
  <div class="main-wrap">
    <div class="title">
      <span class="left">{{dealerid == undefined ? "" : ""}}</span>
      <span class="right"><i>*</i></span>
    </div>
    <el-form :model="form" :rules="FormRules" label-width="150px" class="el-found" ref="formName">
      <el-form-item prop="dealerName" label="">
        <el-input v-model="form.dealerName" placeholder="" maxlength="32"></el-input><br>
        <i class="el-icon-info"></i>
      </el-form-item>
      <el-form-item prop="dealerCode" label="ID">
        <el-input v-model="form.dealerCode" placeholder="ID" maxlength="32"></el-input>
      </el-form-item>
      <el-form-item prop="dealerRel" label="">
        <el-input v-model="form.dealerRel" placeholder="" maxlength="16"></el-input>
      </el-form-item>
      <el-form-item prop="dealerTel" label="">
        <el-input v-model="form.dealerTel" placeholder="" maxlength="16"></el-input>
      </el-form-item>
      <el-form-item prop="status" label="">
        <el-switch active-value="2" inactive-value="1" v-model="form.status">
        </el-switch>
      </el-form-item>
      <el-form-item prop="remarks" label="">
        <el-input type="textarea" placeholder="" maxlength="300" @input="change_num" v-model="form.remarks">
        </el-input>
        <i class="strnum">{{stringnum}}/300</i>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="resetForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { addStore, storeDetail, editStore } from "../../api/api";

export default {
  data() {
    return {
      dealerid: "",
      form: {
        dealerId: "",
        dealerCode: "", // id
        dealerName: "", // 
        dealerRel: "", // 
        dealerTel: "", // 
        status: "", // 
        remarks: "" // 
      },
      FormRules: {
        dealerName: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "",
            trigger: "blur"
          }
        ],
        dealerCode: [
          { required: true, message: "ID", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "ID",
            trigger: "blur"
          }
        ],
        dealerRel: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^.*[^ ].*$/,
            message: "",
            trigger: "blur"
          }
        ],
        dealerTel: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        status: [{ required: true, message: "", trigger: "blur" }],
        remarks: []
      },
      stringnum: 0
    };
  },
  mounted() {
    // dealerid
    this.dealerid = this.$route.query.dealerid;
    this.mianload();
  },
  methods: {
    // 
    mianload() {
      if (this.dealerid) {
        let para = {
          dealerId: this.dealerid
        };
        storeDetail(para).then(res => {
          if (res.header.code !== 10000000) {
            this.$message({
              offset: 100,
              message: res.header.message,
              type: "error"
            });
          } else {
            this.form.dealerCode = res.body.dealerCode;
            this.form.dealerName = res.body.dealerName;
            this.form.dealerRel = res.body.dealerRel;
            this.form.dealerTel = res.body.dealerTel;
            this.form.status = res.body.status;
            this.form.remarks = res.body.remarks;
            this.change_num()
          }
        });
      }
    },
    // 
    change_num() {
      this.stringnum = this.form.remarks.length;
    },
    // 
    onSubmit() {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          let para = this.form;
          if (this.dealerid) {
            // 
            para.dealerId = this.dealerid;
            editStore(para).then(res => {
              if (res.header.code !== 10000000) {
                this.$message({
                  offset: 100,
                  message: res.header.message,
                  type: "error"
                });
              } else {
                this.$message({
                  offset: 100,
                  message: "",
                  type: "success"
                });
                let _this = this;
                setTimeout(function() {
                  _this.$router.push({
                    path: "/storelist"
                  });
                }, 1000);
              }
            });
          } else {
            // 
            addStore(para).then(res => {
              if (res.header.code !== 10000000) {
                this.$message({
                  offset: 100,
                  message: res.header.message,
                  type: "error"
                });
              } else {
                this.$message({
                  offset: 100,
                  message: "",
                  type: "success"
                });
                let _this = this;
                setTimeout(function() {
                  _this.$router.push({
                    path: "/storelist"
                  });
                }, 1000);
              }
            });
          }
        } else {
          this.$message({
            offset: 100,
            type: "warning",
            message: "!"
          });
        }
      });
    },
    // 
    resetForm() {
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          this.$router.push({
            path: "/storelist"
          });
        })
        .catch(() => {});
    }
  },
  beforeRouteEnter(to, from, next) {
    let dealeridx = to.query.dealerid;
    if (dealeridx == undefined) {
      to.meta.title = "";
    } else {
      to.meta.title = "";
    }
    next();
  }
};
</script>
<style lang="less" scoped>
.main-wrap {
  background: #ffffff;
  .title {
    height: 50px;
    line-height: 50px;
    padding: 0 20px;
    border-bottom: 1px solid #e9e9e9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      font-weight: 700;
      font-size: 16px;
    }
    .right {
      color: #999999;
      i {
        margin-right: 5px;
        color: red;
      }
    }
  }
  .el-form {
    padding: 50px 100px;
    .el-form-item {
      position: relative;
      .el-input {
        width: 30%;
      }
      .el-icon-info {
        font-size: 12px;
        color: #a1a1a1;
      }
      .el-textarea {
        width: 500px;
        height: 120px;
        border: 1px solid #d7d7d7;
        border-radius: 3px;
      }
      .strnum {
        position: absolute;
        bottom: 20px;
        left: 408px;
        color: #e0e0e0;
        height: 10px;
      }
    }
  }
}
</style>



